import { Component, OnInit, Output, EventEmitter, ViewChild, Inject } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { UserService, CurrentUserService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxListBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxlistbox';
import { jqxInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxinput';
import { jqxPasswordInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxpasswordinput';

@Component({
  selector: 'app-user-add',
  templateUrl: './user-add.component.html',
  styleUrls: ['./user-add.component.scss']
})
export class UserAddComponent implements OnInit {

  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('roleCombo') roleCombo: jqxComboBoxComponent;
  @ViewChild('estateListBox') estateListBox: jqxListBoxComponent;
  @ViewChild('passwordInput') passwordInput: jqxPasswordInputComponent;
  @ViewChild('confirmPasswordInput') confirmPasswordInput: jqxPasswordInputComponent;

  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  userForm: FormGroup;
  apiUrl: string;
  roleSource: any;
  roleAdapter: any;
  estateSource: any;
  estateAdapter: any;

  userData:any = {};

  constructor(
    private fb: FormBuilder,
    private usrs: UserService,
    @Inject('API_URL') apiUrl: string,
    private cus: CurrentUserService
  ) {
    this.createForm();
    this.userData = this.cus.getTokenData();
    this.apiUrl = apiUrl;
  }

  ngOnInit() {
    this.roleSource =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'display_name' },
        ],
        data: this.userData,
        async: false,
        url: this.apiUrl + 'role?limit=all',
      };
    this.roleAdapter = new jqx.dataAdapter(this.roleSource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        // console.log(data);  
        return tData;
      }
    });
    this.estateSource =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        async: false,
        url: this.apiUrl + 'estate?limit=all',
      };
    this.estateAdapter = new jqx.dataAdapter(this.estateSource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        // console.log(data);  
        return tData;
      }
    });
  }

  createForm() {
    this.userForm = this.fb.group({
      'username' : [null, Validators.required],
      'email': [null, Validators.required],
      'password': [null, Validators.required],
      'password_confirmation': [null, Validators.required],
    });
  }

  saveBtn(post) {
    this.myValidator.validate(document.getElementById('userForm'));
    let estateList = this.estateListBox.getCheckedItems();
    post['estate_id'] = [];
    for (let i = 0; i < estateList.length; i++) {
      post['estate_id'].push(String(estateList[i].value));
    }
    post['role_id'] = this.roleCombo.val();
    if (post['role_id'] && post['estate_id']) {
      this.jqxLoader.open();
      this.usrs.store(post).subscribe(
        result => {
          let data = {
            result: true,
            message: result['message']
          }
          this.AddedData.emit(data);
          this.jqxLoader.close();
        },
        error => {
          let data = {
            result:false,
            message:error['error']['error']['message']
          }
          this.AddedData.emit(data);
          this.jqxLoader.close();
        });
    }
  }

  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
  [ 
    { input: '.username', message: 'Username is required !', action: 'keyup, blur', rule: 'required' },
    { input: '.email', message: 'Email is required !', action: 'keyup, blur', rule: 'required' },
    { input: '.password', message: 'Password is required!', action: 'keyup, blur', rule: 'required' },
    { input: '.password_confirmation', message: 'Confirm Password is required!', action: 'keyup, blur', rule: 'required' },
    {
      input: '.password_confirmation', message: 'Passwords doesn\'t match!', action: 'keyup, focus',
      rule: (input: any, commit: any): boolean => {
        if (this.passwordInput.val() === this.confirmPasswordInput.val()) {
          return true;
        }
        return false;
      }
    },
    {
      input: '.role', message: 'Role is required!', action: 'keyup, focus', rule: (input: any, commit: any): any => {
        let data = this.roleCombo.val();
        let result = (data) ? true : false;
        return result;
      }
    }
  ];

}



